import React, { Fragment } from 'react';

import Section, { SubSection } from '../Section';
import Image from '../Image';

import cardSortImage from '../../images/p2cardsort.jpg';
import sitemapImage from '../../images/p2newsitemap.jpg';
import initialDesignImage from '../../images/p2initialdesigns.jpg';
import paperPrototypeImage from '../../images/p2paperproto.jpg';

const Ideation = () => (
  <Section shade={2} title="Ideation">
    <SubSection title="Sitemap Redesign">
      <p>
        The current top level navigation had many tabs that hid the most useful
        aspects of the product. Surfacing useful features and simplifying IA was
        important to help ensure the users found value in the product and could
        accomplish their tasks. Throughout the early stages of the product, I
        tweaked the new navigation system ultimately arriving with this
        solution:
      </p>
      <iframe
        src="https://giphy.com/embed/YRnsJgN6xgsHYUbaFu"
        width="480"
        height="286"
        frameBorder="0"
        class="giphy-embed"
        allowFullScreen
      />
      <p>
        <a href="https://giphy.com/gifs/YRnsJgN6xgsHYUbaFu">via GIPHY</a>
      </p>{' '}
    </SubSection>

    <hr />

    <SubSection title="Sketches to Blockframes to Wireframes">
      <p>
        I worked on a number of initial sketches to conceptualize designs that I
        could workshop with the team. These sketches allowed quick feedback that
        I could iterate on and help establish confidence in my direction. Then,
        I moved to working directly in Figma creating blockframes so I could
        determine the correct layout for pages.
      </p>
      <Image alt="Initial Design" src={initialDesignImage} />
    </SubSection>

    <hr />
  </Section>
);

export default Ideation;



